
@import "./variable.scss";


// ------------- default variable ----------------------
:root {
  --sidebar: var(--sidebar-large-width);
}


// ------------ change sidebar width --------------------
[data-sidebar="large"] {
  --sidebar: var(--sidebar-large-width);
}
[data-sidebar="small"] {
  --sidebar: var(--sidebar-small-width);
}
